<template>
    <div class="payCharge">
        <el-form :model="applyForm" :show-message="false" size="small" label-width="100px" label-position="left">
            <div class="content">
                <p class="title">{{$L('填写充值金额')}}</p>
                <div class="money-box">
                    <el-form-item prop="money"  :label="$L('充值账户')">
                        <p class="money-item" v-if="tableData.hasOwnProperty('username')">{{ tableData.username }} </p>
                    </el-form-item>
                    <el-form-item prop="money" :label="$L('充值金额')">
                        <p class="money-item"><el-input size="small" v-model="applyForm.money" placeholder="" class="money"></el-input></p>
                    </el-form-item>
                    <el-form-item prop="money" :label="$L('会员备注')">
                        <p class="money-item">
                            <el-input type="textarea" v-model="applyForm.remarks" :rows="6" resize="none" class="remarks"></el-input>
                        </p>
                    </el-form-item>
                </div>
                <p class="title">{{$L('选择支付方式')}}</p>
                <span class="font-grey" v-if="payFeeCount"> {{$L('手续费')}}：{{ payFeeFunc(payFee) }}</span>
                <ul class="pay-methods" v-if="tableData.hasOwnProperty('accountList') && tableData.accountList.length > 0">
                    <li class="pay-li" v-for="(item,index) in tableData.accountList" :key="index">
                        <el-radio-group v-model="applyForm.radio" @change="selectFunc(item.payId,item.payFee)">
                            <el-radio :label="item.payId">
                                <img :src="item.img" alt="">
                            </el-radio>
                        </el-radio-group>
                    </li>
                </ul>
                <div class="tips" v-else>
                    {{ $L('暂无充值方式！请联系我司！') }}
                </div>
                <div class="bottom">
                    <el-form-item>
                        <el-button size="medium" @click="backFunc">{{$L('返回')}}</el-button>
                        <el-button type="primary" size="medium" @click="applyFunc" :disabled="!(tableData.hasOwnProperty('accountList') && tableData.accountList.length > 0)">{{$L('立即充值')}}</el-button>
                    </el-form-item>
                </div>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "payCharge",

        data() {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                payFee: 0,
                payFeeCount:false,
                tableData:{},
                applyForm:{
                    money: '',
                    remarks:'',
                    radio: 0,
                },
            }
        },
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            backFunc(){
                this.$router.push({name:'payRecord'});
            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('paymentOnline', {} , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
						//把后台传过来对象数据转成数组
						let arr = [];
						for (let i in reData.data.accountList) {
							arr.push(reData.data.accountList[i]);
						}
						_self.tableData.accountList = arr;
                        _self.applyForm.radio = _self.tableData.accountList[0]['payId'];
                        if(_self.tableData.accountList[0]['payFee'] !== "0" ){
                            _self.payFeeCount = true;
                            _self.payFee = _self.tableData.accountList[0]['payFee'];
                        } else {
                            _self.payFeeCount = false;
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.backFunc();
                    });
                });
            },
            selectFunc(id,fee){
                const _self = this;
                _self.radio = id;
                if(fee !== "0" ){
                    _self.payFeeCount = true;
                    _self.payFee = fee;
                } else {
                    _self.payFeeCount = false;
                }
            },
            applyFunc(){
                const _self = this;
                if(this.applyForm.money==='' || isNaN(parseFloat(this.applyForm.money))){
                    this.$message({
                        type: 'error',
                        message: _self.$L('充值金额与支付方式不能为空!')                    });
                    return;
                }
                this.applyForm.money = parseFloat(this.applyForm.money);
                if(this.applyForm.radio === 0){
                    this.$message({
                        type: 'error',
                        message: this.$L('请选择支付方式!')
                    });
                    return;
                }
                this.submitFunc();
            },
            payFeeFunc(val){
                if(val.indexOf('%') < 0){
                    val = val+this.$L('元');
                }
                return val;
            },
            submitFunc(){
                const _self = this;
                _self.loading = true;
                let arr = {
                    amount: _self.applyForm.money,
                    remarks: _self.applyForm.remarks,
                    payId: _self.applyForm.radio,
                }
                _self.$api('checkoutPay', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.$router.push({
                            name:'payConfirm',
                            query:{accountId: reData.data.accountId},
                        });
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.backFunc();
                    });
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    .payCharge{
        padding: 20px;
        font-size: 14px;
        .content{
            margin: 10px 0;
            background-color: #fff;
            .title{
                font-size: 16px;
                padding-left: 20px;
                line-height: 50px;
                display: inline-block;
            }
            .font-grey{
                padding-left: 20px;
                color: #999;
            }
            .money-box{
                border-top: 1px solid #ccc;
                padding: 20px ;
                .money-item{
                    /*padding: 10px 0;*/
                }
                /deep/ .el-form-item{
                    margin-bottom: 20px;
                }
                .money{
                    width: 250px;
                }
                .remarks{
                    width: 400px;
                    vertical-align: middle;
                    font-size: 14px;
                }
            }
            .pay-methods{
                margin: 0 20px;
                border: 1px solid #ccc;
                display: -webkit-flex;
                justify-content: flex-start;
                padding: 40px 0 40px 35px;
                flex-wrap: wrap;
                .pay-li{
                    margin-right: 40px;
                    margin-bottom: 20px;
                    label{
                        cursor: pointer;
                        img{
                            vertical-align: middle;
                        }
                    }
                }
            }
            .tips{
                margin: 0 20px;
                position: relative;
                height: 50px;
                line-height: 50px;
                background-color: #ffffff;
                font-size: 16px;
                color: #999;
            }
            .bottom{
                text-align: right;
                padding: 30px 0;
                margin: 0 20px;
                .el-button{
                    width: 120px;
                    height: 40px;
                }
                .el-button:last-child{
                    margin-left: 30px;
                }
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>